@import "neat-helpers"; // or "../neat/neat-helpers" when not in Rails

// Neat Overrides
 $column: 235px;
 $gutter: 36px;
 $grid-columns: 3 ;
 $max-width: 1200px;

// Neat Breakpoints
$medium-screen: 600px;
$large-screen: 900px;

$medium-screen-down: new-breakpoint(max-width $medium-screen 4);
$medium-screen-up: new-breakpoint(min-width $medium-screen 4);
$large-screen-up: new-breakpoint(min-width $large-screen 12);
