
///////////////////////////original color///////////////////////////////


$color-plart-yellow: #e6ff00;
$color-plart-gray: #dcdcdc;
$color-plart-black: #000000;
$color-plart-lightgray:#e4e4e4;


////////////// basic size /////////////

$box-padding: 36px;
$box-padding-topbottom: 28px;
$box-padding-small: 26px;

$box-border-width:5px;
$box-border-width-thin:2px;

$box-padding-sp: 13px;

//////////////topbar/////////////


$topbar-height:auto;

$body-side-margin:1rem;

///////////////////////////BITTERS///////////////////////////////

// Typography
$base-font-family: "Helvetica Neue",YuGothic, "Yu Gothic", "游ゴシック", "游ゴシック体", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", sans-serif;
$base-font-weight: normal;

$heading-font-family:'Montserrat', sans-serif;
//$heading-font-family:"Helvetica Neue", Arial, sans-serif,"游ゴシック Medium", "Yu Gothic Medium", "游ゴシック体", YuGothic;
$heading-font-weight: bold;

// Font Sizes
$pc-font-size: 62.5%;    // = 10px => 1.6rem = 16px
$tablet-font-size:$pc-font-size;// おなじでよくね
$mobile-font-size:$pc-font-size;// おなじでよくね

$base-font-size: 1.6rem; // パソコンは16px

$font-size-small:1rem;
$font-size-normal:1.4rem;
$font-size-medium:1.5rem;
$font-size-semibig:1.8rem;
$font-size-big:2.6rem;
$font-size-extrabig:2.8rem;

// Line height
$base-line-height: 2;
$heading-line-height: 1.2;

// LETTER SPACING
$base-letter-spacing: 0.13em;
$small-letter-spacing: 0.1em;

// Other Sizes
$base-border-radius: 3px;
$base-spacing: $base-line-height * 1em;
$small-spacing: $base-spacing / 2;
$base-z-index: 0;

// Colors
$blue: #477dca;
$dark-gray: #333;
$medium-gray: #999;
$light-gray: #ddd;

// Font Colors
$base-font-color: $dark-gray;
$action-color: $color-plart-yellow;

// Border
$base-border-color: $light-gray;
$base-border: 1px solid $base-border-color;

// Background Colors
$base-background-color: #fff;
$secondary-background-color: tint($base-border-color, 75%);

// Forms
$form-box-shadow: inset 0 1px 3px rgba(#000, 0.06);
$form-box-shadow-focus: $form-box-shadow, 0 0 5px adjust-color($action-color, $lightness: -5%, $alpha: -0.3);

// Animations
$base-duration: 150ms;
$base-timing: ease;




